@use "global" as *;

.page-about-gallery__container {
  display: grid;
  gap: rem(8);
  margin-top: rem(41);
  position: relative;

  @include mq(md) {
    grid-template-columns: 45.555% 7.4% auto;
    grid-template-rows: auto;
    margin-top: rem(80);
    grid-auto-flow: dense;
  }
}

.page-about-gallery__container::after {
  content: '';
  position: absolute;
  display: none;
  width: rem(301);
  height: rem(138);
  background-image: url(../images/common/common-illustration.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  right: calc(50% - min(48vw, rem(640)));
  top: rem(-228);

  @include mq(md) {
  display: block;
  }
}

.page-about-gallery__image {
  cursor: pointer;
}

.page-about-gallery__image:nth-child(6n+1),
.page-about-gallery__image:nth-child(6n+6) {

  @include mq(md) {
    grid-column: span 1;
    grid-row: span 2;
  }
}

.page-about-gallery__image:nth-child(6n+1) img,
.page-about-gallery__image:nth-child(6n+6) img {
  aspect-ratio: 345/523;
  object-fit: cover;
  height: auto;
}

.page-about-gallery__image:nth-child(6n+2),
.page-about-gallery__image:nth-child(6n+3),
.page-about-gallery__image:nth-child(6n+4),
.page-about-gallery__image:nth-child(6n+5) {

  @include mq(md) {
    grid-column: span 2;
    grid-row: span 1;
  }
}

.page-about-gallery__image:nth-child(6n+2) img,
.page-about-gallery__image:nth-child(6n+3) img,
.page-about-gallery__image:nth-child(6n+4) img,
.page-about-gallery__image:nth-child(6n+5) img {
  aspect-ratio: 345/219;
  object-fit: cover;
  height: auto;
}