@use "global" as *;

.sidebar-campaign-card {
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
  -webkit-backface-visibility: hidden;
  -webkit-transform: translate3d(0, 0, 0);
  -webkit-transform-style: preserve-3d;
}

.sidebar-campaign-card__item {
  display: flex;
  flex-direction: column;
  background-color: $white;
  height: 100%;
}

.sidebar-campaign-card__img {
  width: 100%;
  overflow: hidden;
}

.sidebar-campaign-card__img img {
  aspect-ratio: 294/188;
  object-fit: cover;
  height: auto;
}

.sidebar-campaign-card__body {
  padding: rem(15) rem(16) rem(22);
  display: flex;
  flex-direction: column;
  flex-grow: 1;

  @include mq(md) {
    padding: rem(15) rem(23) rem(22);
  }
}

.sidebar-campaign-card__title-main {
  font-family: $base-font-family;
  font-size: rem(16);
  font-weight: $medium;
  line-height: calc(24/ 16);
  color: $title-color;
  border-bottom: 1px solid $accent-color;
  padding-bottom: rem(8);
  flex-grow: 1;
  text-align: center;

  @include mq(md) {
    padding: rem(4) 0 rem(7);
  }
}

.sidebar-campaign-card__title-sub {
  font-family: $base-font-family;
  font-size: rem(14);
  font-weight: $regular;
  line-height: calc(24/ 14);
  color: $title-color;
  text-align: center;
  margin-top: rem(8);

  @include mq(md) {
    margin-top: rem(6);
  }
}

.sidebar-campaign-card__price {
  font-family: $third-font-family;
  font-size: rem(32);
  font-weight: $bold;
  line-height: 1.1;
  color: $accent-color;
  margin-top: rem(6);
  text-align: center;
}

.sidebar-campaign-card__price span {
  font-family: $third-font-family;
  font-size: rem(20);
  font-weight: $bold;
  line-height: 1;
  color: $title-color;
  display: inline-block;
  margin-right: rem(18);
  text-decoration: line-through 3px solid $point-color;
}